/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/

body,
button,
input,
select,
textarea {
	color: $color__text-main;
	font-family: $font__main;
	font-size: 1rem;
	line-height: $font__line-height-body;
}

/* Headings */

h1, h2, h3, h4, h5, h6 {
	clear: both;
}

/* Copy */

p {
	margin-bottom: $base__space-unit;
}

dfn, cite, em, i {
	font-style: italic;
}

blockquote {
	margin: 0 $base__space-unit;
}

address {
	margin: 0 0 $base__space-unit;
}

pre {
	background: $color__background-pre;
	font-family: $font__pre;
	font-size: .9375rem;
	line-height: $font__line-height-pre;
	margin-bottom: 1.6rem;
	max-width: 100%;
	overflow: auto;
	padding: 1.6rem;
}

code, kbd, tt, var {
	font-family: $font__code;
	font-size: .9375rem;
}

abbr, acronym {
	border-bottom: 1px dotted $color__border-abbr;
	cursor: help;
}

mark, ins {
	text-decoration: none;
}

big {
	font-size: 125%;
}

.index-entry-content { // the quote
	font-size: 1.5rem;
}

.entry-meta{	
	.entry-title { // the author credit
		display: inline-block;
		font-size: 1.25rem;
	}
	.source {
		font-size: 1.25rem;
	}
}

.main-navigation {
	a:nth-child(1) {
		padding-right: $base__space-unit/2;
		border-right: 1px solid $color__text-main;
		margin-right: $base__space-unit/2;
	}
}
